// Core
import React from 'react';
import PropTypes from 'prop-types';

// Stylesheet
import styles from './index.module.scss';
import Tab from './Tab';

const DetailsColumn = ({ activeTab, setActiveTab }) => {
    return (
        <article className={`column is-6`}>
            <header className={`${styles.header}`}>
                <h2 className={`${styles.headerTitle} title is-family-secondary is-size-3 is-size-2-desktop is-spaced`}>
                    <strong className={styles.headerTitleBold}>Heraldbee</strong> - Twj osobisty konfigurator rozwiza dla ecommerce
                </h2>
                <p className={`${styles.headerSubtitle} subtitle is-size-5 is-size-4-desktop`}>Zbudujemy sklep, ktry przycignie nowych klientw i nauczymy Ci nim zarzdza</p>
                <hr className="space is-2"/>
            </header>
            <main className={`${styles.body}`}>
                <div className={styles.container}>
                    <ul className={styles.tabs}>
                        <Tab
                            step={1}
                            activeTab={activeTab}
                            setActiveTab={setActiveTab}
                            title="Skonfigurujemy wszystko za Ciebie"
                            description="Otrzymasz indywidaln grafik sklepu. Dobierzemy najlepsze wtyczki i konfiguracj. Regulamin. Polityka prywatnoci. RODO. Pokaemy Ci jakie to proste."
                        />
                        <Tab
                            step={2}
                            activeTab={activeTab}
                            setActiveTab={setActiveTab}
                            title="Usugi antyagencyjne"
                            description="Zbudujemy Ci zautomatyzowane formy reklamy i nauczymy Ci nimi zarzdza. Nigdy reklama nie bya taka prosta!"
                        />
                        <Tab
                            step={3}
                            activeTab={activeTab}
                            setActiveTab={setActiveTab}
                            title="100% Twojego budetu reklamowego trafia na reklam"
                            description="Nikt nie bdzie pobiera prowizji za obsug Twoich reklam."
                        />
                        <Tab
                            step={4}
                            activeTab={activeTab}
                            setActiveTab={setActiveTab}
                            title="Zaufaj ekspertom"
                            description="Z nami zarzdzanie sklepem bdzie prostsze. Implementujc aplikacj Heraldbee dostaniesz dostp do szerokiej bazy wiedzy, ktra pozwoli Ci rozwija swj sklep."
                        />
                    </ul>
                </div>
            </main>
        </article>
    );
};

DetailsColumn.propTypes = {
    activeTab: PropTypes.number.isRequired,
    setActiveTab: PropTypes.func.isRequired
};

export default DetailsColumn;